import VueRouter from 'vue-router'
import Vue from 'vue'

import MyHomePage from '../pages/home/MyHomePage'
import MyCategoriesPage from '../pages/categoties/MyCategoriesPage'
import MyShopPage from '../pages/shop/MyShopPage'
import MyProfilePage from '../pages/profile/MyProfilePage'
import MyGodsDetailePage from '../pages/categoties/MyGodsDetailePage'
import MyStudyPage from '../pages/mystudy/MyStudyPage'
import MyVuexPage from '../pages/mystudy/MyVuexPage'
import MyCSSAnimationPage from '@/pages/mystudy/MyCSSAnimationPage'
import MyStoragePage from '@/pages/mystudy/MyStoragePage'
import MyDrawingPage from '@/pages/mystudy/MyDrawingPage'

Vue.use(VueRouter)

const routes = [
    { path: '/', redirect: '/home'},
    // meta
    { path: '/home', name:'home', component: MyHomePage, meta:{hideTabBar:false}},
    { path: '/categories', name:'', component: MyCategoriesPage, meta:{hideTabBar:false}},
    { path: '/shop', name:'', component: MyShopPage, meta:{hideTabBar:false}},
    { path: '/me', name:'', component: MyProfilePage, meta:{hideTabBar:false}, children:[
        {
            path:'study',
            name:'',
            component:MyStudyPage
        },
        {
            path:'vuex',
            name:'Vuex',
            component:MyVuexPage
        }
    ]},
    { path: '/detail/:goodsId', name:'', component: MyGodsDetailePage, meta:{hideTabBar:true}},
    { path: '/animation', name:'CSS', component:MyCSSAnimationPage, meta:{hideTabBar:true}},
    { path: '/storage', name:'', component:MyStoragePage, meta:{hideTabBar:true}},
    { path: '/drawing', name:'', component:MyDrawingPage, meta:{hideTabBar:true}}
]

const router = new VueRouter({
    routes // ()  routes: routes
})

router.beforeEach((to, from, next)=>{
    document.title = to.name
    console.log('++++++', from)
    next()
    // 
    // next({ name: 'home' })
    // next({ path: '/home', query: {} })
})
router.afterEach((to, from)=>{
    console.log(to,'------',from)
})

export default router