<template>
    <div class="parent-view">
        <p>{{ info.name }} ---- {{ info.age }}</p>
        <button @click="onAgeChanged"></button>
        <h2></h2>
        <MyChildrenView></MyChildrenView>
    </div>
</template>


<script>
import MyChildrenView from './MyChildrenView'
export default {
    name:'ParentView',
    components: { MyChildrenView },
    data() {
        return {
            flag:'',
            info:{
                name:'Ape',
                age:28,
                sex:'male'
            }
        }
    },
    provide(){
        return {
            userInfo:this.info,
        }
    },
    methods: {
        onAgeChanged() {
            this.info.age += 1
        }
    }
}

</script>


