import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import * as Cookies from "js-cookie"

import TYPES from './types'

Vue.use(Vuex)

/**
 *  stategetterscomputed

 *  statemutations

 *  actions
 */
const store = new Vuex.Store({
  // module
    modules:{
      count:{
        state: {
          // modulesstatestate.moduleName.property
          counter:0
        },
        mutations:{
          // modulesthis.$store.commit('method',params...)
          /**
           *  mutations

           *  mutationsmutations state

           * mutationsaxiossetTimeoutmutationsstate

           * mutations,
           */
          [TYPES.INCREASE_COUNTER](state) {
            state.counter++
          },
          decrease(state) {
            state.counter--
          },
          increaseCount(state, playload) {
            state.counter += playload
          },
        },
        getters: {
          // modules$store.getters.method(params)
          powerCounter(state) {
            return state.counter * state.counter
          }
        },
        actions:{
          // modulesthis.$store.dispatch('method', params...)
          /**
           * 1. actionstatemutation
           * 2. actionaxiosaction
           * 3. actionpromise
           */
          increaseAction(context, playload) {
            setTimeout(() => {
              context.commit('increaseCount', playload)
            }, 1000);
          }
        },
      },
      student:{
        state:{
          students:[
            {id:1101, name:"jacky", age:18},
            {id:1102, name:"nancy", age:20},
            {id:1103, mame:"judy", age:19},
            {id:1104, name:"alon", age:21}
          ]
        },
        mutations: {
          [TYPES.ADD_STUDENT](state, stu) {
            state.students.push(stu)
          },
          updateInfo(state, height) {
            state.info.height = height
          }
        },
        getters: {
          more20stu(state) {
            return state.students.filter(s=> s.age > 20)
          },
          more20stuLength(state, getters) {
            return getters.more20stu.length
          },
          moreAgeStu(state) {
            return age=>{
              return state.students.filter(s=>s.age>age)
            }
          }
        }
      },
      project: {
        state: {
          list:[]
        },
        mutations:{
          updateList(state, list) {
            state.list = list
          }
        }
      }
    },
    state: {
        info: {
          name: "sandy",
          age:35,
          height: 1.88
        }
    },
    mutations: {

    },
    getters:{

    },
    plugins: [
      // createPersistedState
      createPersistedState({
        // localStorage
        storage: window.sessionStorage,
        // key: 'my_store', // keystatekeyvuex
        // paths: ['count'], // 
        // pathreducerreducer, paths
        reducer(state) {
          return {
            // state
            // info:state.info,
            count:state.count,
            student:state.student
          }
        }
      }),
      createPersistedState({
        storage: window.localStorage,
        key: 'my_info',
        paths:['info'],
      }),
      // createPersistedState({
      //   // cookiegetItemsetItemremoveItem
      //   storage: {
      //     getItem: (key) => Cookies.get(key),
      //     setItem: (key, value) =>
      //       Cookies.set(key, value, { expires: 3, secure: true }),
      //     removeItem: (key) => Cookies.remove(key)
      //   }
      // }),
      // createPersistedState({
      //   //
      //   //secure-ls localStorage https://github.com/softvar/secure-ls
      //   storage: {
      //     getItem: (key) => ls.get(key),
      //     setItem: (key, value) => ls.set(key, value),
      //     removeItem: (key) => ls.remove(key)
      //   }
      // })
    ]
})

export default store